$GREEN: #0EC261;
$BLACK: #121212;
$GRAY: #999;
$LGRAY: #EEE;

$BLUE: #007BFF;
$RED: #CB3656;
$GRAPH: #3D5170;

@mixin mainbtn ($color1, $color2) {
	background: linear-gradient( to right, $color1 , $color2);
	font-weight: 400;
	height: 48px;
	font-size: 16px;
	line-height: 21px;
	color: #fff;
	border-radius: 6px;
	border: none;
	cursor: pointer;
	transition: 0.3s linear;
	&:hover {
		transition: 0.3S linear;
		box-shadow: inset 0px 0px 85px rgba(0,0,0,0.2);
 }		// background: linear-gradient( to right, darken($color1, 10%), darken($color2, 10%))
	&:focus {
		outline: none;
		box-shadow: inset 0px 0px 85px rgba(0,0,0,0.4); } }
